.Hero {
  min-height: 100vh;
  position: relative;
  overflow: hidden;
}

.Hero .HeroSlider {
  position: absolute;
  transition: 1s;
  display: flex;
}

.Hero .slide {
  position: relative;
  width: 100vw;
  min-height: 100vh;
  display: flex;
  align-items: center;
  color: #fff;
}

.Hero .slide .slideContent {
  width: 1140px;
  margin: 5rem auto 10rem;
  position: relative;
}

.Hero .slide .slideContent .badge {
  padding: 5px 10px;
  background-color: #043a5b;
  display: inline-block;
  margin-bottom: 1rem;
}

.Hero .slide .slideContent h1 {
  font-weight: 900;
  letter-spacing: 1.1px;
  font-size: 3rem;
  max-width: 600px;
}

.Hero .slide .slideContent .slideSubContent {
  margin-left: 5rem;
  margin-top: 1rem;
  border-left: 1px solid #a7a428;
  padding: 0.5rem 0rem 0.5rem 1rem;
  padding-left: 1rem;
  max-width: 350px;
}

.Hero .slide .slideContent .slideSubContent p {
  line-height: 1.6;
  letter-spacing: 1px;
}

.Hero .slide .slideContent .slideSubContent button {
  margin-top: 1rem;
  padding: 0.5rem 1rem;
  background-color: #a7a428;
  color: #fff;
  font-weight: 900;
  text-transform: uppercase;
  letter-spacing: 1.2px;
}

.Hero .slide .slideContent .slideSubContent button i {
  margin-left: auto;
}

.Hero .slide.active {
  left: 0;
}

.Hero .slide.active .slideContent .badge {
  -webkit-animation: 0.6s fadeTop;
          animation: 0.6s fadeTop;
}

.Hero .slide.active .slideContent h1 {
  -webkit-animation: 0.8s fadeTop;
          animation: 0.8s fadeTop;
}

.Hero .slide.active .slideContent .slideSubContent p {
  -webkit-animation: 1s fadeTop;
          animation: 1s fadeTop;
}

.Hero .slide.active .slideContent .slideSubContent button {
  -webkit-animation: 1.2s fadeTop;
          animation: 1.2s fadeTop;
}

.Hero .slideCounter {
  position: absolute;
  bottom: 10%;
  color: #fff;
  width: 1140px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
.Hero .slideCounter.slideCounterBlue {
  color: #172134;
}
.Hero .slideCounter.slideCounterBlue .slideCounts::after {
  background: #172134;
}


.Hero .slideCounter .slideCounts {
  border-bottom: 3px solid #aaa;
  width: 200px;
  padding-bottom: 0.5rem;
  position: relative;
}

.Hero .slideCounter .slideCounts::after {
  content: "";
  position: absolute;
  width: 33.33%;
  height: 3px;
  background: #fff;
  bottom: -3px;
  left: 0;
  transition: 0.7s;
}

.Hero .slideCounter .slideCounts.slideO::after {
  width: 66.66%;
}

.Hero .slideCounter .slideCounts.slideT::after {
  width: 100%;
}

.Hero .slideCounter .prevNext {
  display: flex;
  justify-content: space-between;
  width: 200px;
  align-items: center;
  margin-top: 0.5rem;
}

.Hero .slideCounter .prevNext span {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.Hero .slideCounter .prevNext span i {
  margin: 0 0.5rem;
  margin-top: 0.15rem;
}

.slideImg {
  width: 100%;
  min-height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
}

.slideImg::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.3);
  width: 100%;
  height: 100%;
}

.slideImg img {
  object-fit: cover;
  width: 100%;
  height: 100vh;
}

@-webkit-keyframes fadeTop {
  from {
    opacity: 0;
    -webkit-transform: translateY(300px);
            transform: translateY(300px);
    border-left: none;
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes fadeTop {
  from {
    opacity: 0;
    -webkit-transform: translateY(300px);
            transform: translateY(300px);
    border-left: none;
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@media (max-width: 1200px) {
  .Hero .slide .slideContent {
    width: 90%;
  }
  .Hero .slideCounter {
    width: 80%;
  }
}

@media (max-width: 820px) {
  .Hero .slide .slideContent h1 {
    font-size: 1.8rem;
  }
  .Hero .slide .slideContent .slideSubContent {
